.container {
  max-width: 1280px;
  margin: 0 auto;
}
body {
  margin: 0;
  padding: 0;
  background-color: rgb(89, 100, 122);
}

.NavBar {
  background-color: #000000;
  height: 70px;

  /* grid-template-columns: 1fr 1fr; */
  /* padding: 0 15px; */
}

.navbar_wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
}


.container {
      max-width: 1280px;
        display: flex;
  justify-content: space-between;
  padding: 0 20px;
  align-items: center;
  height: 100%;
  margin: 0 auto;
      flex-wrap: wrap;


}
.NavBar img {
  cursor: fixed;
}

.NavBar h3 {
  color: rgb(255, 255, 255);
}
.NavBar button {
  cursor: pointer;

  width: 115px;
  justify-self: end;
  padding: 5px 0;
  border: 1px solid transparent;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
}

.menu_buttons {
  display: flex;
  grid-gap: 0;
  gap: 0;
}

.hamburger-react {
  display: none;
}

.menu_buttons span {
  font-weight: 600;
  font-size: 18px;
  cursor: pointer;
  color: #ffffff;
}

.menu_buttons span:hover {
  text-decoration: underline;
  color: #ffffff90;
}

.slider {
  background-color: #000000;
  display: none;
  flex-direction: column;
  position: fixed;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
  padding-top: 100px;
  color: #ffffff;
  font-size: 23px;
  height: 100vh;
  width: 100vw;
  z-index: 20;
  transition: all 0.6s;
  right: -700px;
}

.open {
  right: 0;
}

