.footer {
  z-index: 15;
  background-color: #000000;
  width: 100%;
  position: fixed;
  bottom: 0;
  height: 70px;
  color: white;
}

.footer_container {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0px 20px;
      max-width: 1280px;
      flex-wrap: wrap;
  margin: 0 auto;

}

.social_buttons_container {
  margin-left: auto;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
} 

.social_buttons_container img:hover {
  transition: all ease-in 0.3s;
  transform: rotate(360deg);
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .menu_buttons {
    display: none;
  }

  .slider {
    display: flex;
  }

  .hamburger-react {
    display: flex;
    z-index: 21;
  }

  .NavBar .container {
    display: flex;
    justify-content: center;
  }

  .NavBar {
    height: 100px;

  }

  .footer {
    position: relative;
    margin-top: 20vh;
  }

  .footer_container {
    justify-content: center;
  }

  .social_buttons_container {
    margin-left: 0px;
  }
}


.navbar_link {
  color: white !important;
  font-weight: 600;
  display: flex;
  grid-gap: 15px;
  gap: 15px;
}

.navbar_link a {
  color: white !important;

}

.navbar_link a:hover {
  text-decoration: underline;
  cursor: pointer;
}

.button_hero_container {
  display: flex;
  justify-content: center;
  max-width: 1280px;
  margin: 40px auto;
}

.neon-button {
  background-color: #fff;
font-family: 'Cabin Sketch', cursive;

    border: .125em solid var(--clr-neon);
    border-radius: .25em;
    box-shadow: inset 0 0 .5em 0 var(--clr-neon), 0 0 .5em 0 var(--clr-neon);
    color: #9130f8;
    color: var(--clr-neon);
    cursor: pointer;
    display: inline-block;
    font-size: 1.2rem;
    padding: .75em 1em;
    position: relative;
    text-decoration: none;
    font-weight: 600;
    text-shadow: 0 0 .125em hsla(0, 0%, 100%, .3), 0 0 .45em currentColor;
    z-index: 10;
}

.neon-button:hover,
.neon-button:focus {
  background-color: var(--clr-neon);
    color: #fff;
    text-shadow: none;
    transition: background-color 1s;
}



@media only screen and (max-width: 600px) {
  .button_hero_container {
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
  }

  .neon-button {
    font-size: 0.9rem;
  }
}

:root {
      --clr-neon: #000000;

}

