  justify-content: center;
  display: flex;
  grid-gap: 40px;
  gap: 40px;
  margin-top: 100px;
}

.MintButton {
   background-color: #fff;
font-family: 'Cabin Sketch', cursive;

    border: .125em solid var(--clr-neon);
    border-radius: .25em;
    box-shadow: inset 0 0 .5em 0 var(--clr-neon), 0 0 .5em 0 var(--clr-neon);
    color: #9130f8;
    color: var(--clr-neon);
    cursor: pointer;
    display: inline-block;
    font-size: 1.2rem;
    padding: .75em 1em;
    position: relative;
    text-decoration: none;
    font-weight: 600;
    text-shadow: 0 0 .125em hsla(0, 0%, 100%, .3), 0 0 .45em currentColor;
    z-index: 10;
}

.MintButton:hover,
.MintButton:focus {
  background-color: var(--clr-neon);
    color: #fff;
    text-shadow: none;
