}


.description_container {
  background-color: #000000da;
  max-width: 400px;
  padding: 30px 40px;
  color: #ffffff;
  border-radius: 10px;
  margin: 40px auto;
}

.description_container h2 {
  font-size: 35px;
  line-height: 30px;
}

.description_container p {
  font-size: 17px;
  text-align: justify;
}

.animation {
  position: absolute;
  width: 1280px;
  height: 100vh;
  z-index: -1;
  top: -300px;
  left: 50%;
  transform: translate(-50%, 0);
}

@media only screen and (max-width: 600px) {
  .animation {
    display: none;
  }

  .MintButton {
    font-size: 0.9rem;
  }

  .description_container h2 {
    font-size: 25px;
  }

  .description_container p,
  .description_container li {
    font-size: 12px;
  }
  @media only screen and (max-width: 600px) {
    .MintButtonContainer {
      flex-wrap: wrap;
      /* margin-top: 0px; */
      justify-content: center;
      grid-gap: 20px;
      gap: 20px;
    }
  }
}

video {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

